
.myimage img{
    height: 150px;
    width: 150px;
    margin: auto;
    border-radius: 100px 100px 100px 100px;
    overflow: hidden;

}
.myimage {
    height: 150px;
    width: 150px;
    border-radius: 100px 100px 100px 100px;
    -webkit-box-shadow: 0px 0px 10px 10px black;
            box-shadow: 0px 0px 10px 10px black;
    
}
.myimgcontainer{
    margin: auto;
    width: 150px;
    margin-top: -100px;
    
  
}

.myimage:hover {
    height: 150px;
    width: 150px;
    -webkit-transform: scale(1.3);
        -ms-transform: scale(1.3);
            transform: scale(1.3);
    border-radius: 100px 100px 100px 100px;
    -webkit-box-shadow: 0px 0px 10px 10px white;
            box-shadow: 0px 0px 10px 10px white;
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    
}


