@media screen and (max-width:2200px){
.cpart{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
   
    background-color: black;
    -ms-flex-direction: column;
        flex-direction: column;
-ms-flex-align: center;
    align-items: center;
-ms-flex-pack: distribute;
    justify-content: space-around;
}
.tpart
{ width: 80%;
