@media screen and (max-width:2200px){
.edu {
    
    width: 100%;
    background-color: rgb(255, 255, 255);
    display: -ms-flexbox;
    display: flex;
    
    
    
}

.in1{
    width: 50%;
   background-color: cornsilk;
    
}
.line{
    border: 5px dotted black;
    height: 90%;
    width: .1%;
    margin: auto;
    margin-top: 10px;
    position: relative;
    top:10px;
    
}
.img1{
    position:-webkit-sticky;
    position:sticky;
    height: 100px;
    width: 100px;
    left: 50%;
    top: 100px;
}
.img1 img{
    position:-webkit-sticky;
    position:sticky;
    height: 100px;
    width: 100px;
    left: 50%;
    top: 100px;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}







.in2{
    background-color: black;
    width: 50%;
}
.inedu {
    background-image: -webkit-gradient(linear,left top, left bottom,from(rgb(252, 0, 0)),to(steelblue));
    background-image: -webkit-linear-gradient(rgb(252, 0, 0),steelblue);
    background-image: -o-linear-gradient(rgb(252, 0, 0),steelblue);
    background-image: linear-gradient(rgb(252, 0, 0),steelblue);
    width: 100%;
    text-align: center;
    color:cyan;
   font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 2.8rem;
    width: 80%;


}

.inedu:hover {
    background-image: -webkit-gradient(linear,left top, left bottom,from(steelblue),to(rgb(252, 0, 0)));
    background-image: -webkit-linear-gradient(steelblue,rgb(252, 0, 0));
    background-image: -o-linear-gradient(steelblue,rgb(252, 0, 0));
    background-image: linear-gradient(steelblue,rgb(252, 0, 0));
    width: 100%;
    text-align: center;
    -webkit-box-shadow: 10px 10px 5px 5px rgb(41, 0, 95);
            box-shadow: 10px 10px 5px 5px rgb(41, 0, 95);
   font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 2.8rem;
    width: 80%;
    -webkit-transition: all 2s;
    -o-transition: all 2s;
    transition: all 2s;
    


}

.inedu  {
    

margin: 50px;
}
}

