@media screen and (max-width:2200px){
.fo{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;

}

.himg{
    margin: 30px;
    height: 300px;
    width: 300px;
    position: relative
}

.himg img{
    height: 300px;
    width: 300px;
}

.htext{
    font-size: 30px;
    position: absolute;
   top: 10px;
    display: none;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.himg:hover>.htext{
    font-size: 40px;
    position: absolute;
    background-color: black;
    top:40%;
    left:20%;
    display: inline;
    color: wheat;
    -webkit-transition: display 1s;
    -o-transition: display 1s;
    transition: display 1s;
    
}}

@media screen and (max-width:900px){
    .fo{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        -ms-flex-direction: column;
            flex-direction: column;
    
    }
    
    .himg{
        margin: 30px;
        height: 300px;
        width: 300px;
        position: relative
    }
    
    .himg img{
        height: 300px;
        width: 300px;
    }
    
    .htext{
        font-size: 30px;
        position: absolute;
       top: 10px;
        display: none;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    }
    
    .himg:hover>.htext{
        font-size: 30px;
        position: absolute;
        background-color: black;
        top:40%;
        left:30%;
        display: inline;
        color: wheat;
        -webkit-transition: display 1s;
        -o-transition: display 1s;
        transition: display 1s;
        
    }}
    
    


