@media screen and (max-width:2200px){
.skills{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
   -ms-flex-wrap: wrap;
       flex-wrap: wrap;
    width: 100%;
    background-color: black;
    -ms-flex-pack: distribute;
        justify-content: space-around;

}

.skilldesign
{margin: 50px;
    width: 300px;
    height: 60px;
    background-color: red;
    border-radius: 2rem 2rem;
    color:white;
    position: relative;
}
  
.skilldesign p
{position: absolute;
    left:40%;
    top:10%;
}

.skilldesign:hover
{margin: 50px;
    width: 300px;
    height: 60px;
    background-color: red;
    border-radius: 2rem 2rem;
    color:white;
    -webkit-transform:scale(1.3);
        -ms-transform:scale(1.3);
            transform:scale(1.3);
    position: relative;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
    
}}

