@media screen and (max-width:2200px){
.btnsection{
    height: 15vh;
    width: 25%;
    margin-top: 50px;
    background-image: -webkit-gradient(linear,left top, left bottom,from(grey),to(white));
    background-image: -webkit-linear-gradient(grey,white);
    background-image: -o-linear-gradient(grey,white);
    background-image: linear-gradient(grey,white);
    font-size: 30px;
    font-family:'Times New Roman', Times, serif;
 
    
}
.btnsection:hover{
    height: 15vh;
    width: 25%;
    margin-top: 20px;
    background-image: -webkit-gradient(linear,left top, left bottom,from(rgb(0, 43, 66)),to(rgb(0, 0, 0)));
    background-image: -webkit-linear-gradient(rgb(0, 43, 66),rgb(0, 0, 0));
    background-image: -o-linear-gradient(rgb(0, 43, 66),rgb(0, 0, 0));
    background-image: linear-gradient(rgb(0, 43, 66),rgb(0, 0, 0));
    color: white;
    font-size: 30px;
    font-family:'Times New Roman', Times, serif;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition: all .8s;
    -o-transition: all .8s;
    transition: all .8s;
    
}}

@media screen and (max-width:1000px){
    .btnsection{
        height: 10vh;
        width: 25%;
        margin-top: 50px;
        background-image: -webkit-gradient(linear,left top, left bottom,from(grey),to(white));
        background-image: -webkit-linear-gradient(grey,white);
        background-image: -o-linear-gradient(grey,white);
        background-image: linear-gradient(grey,white);
        font-size: 10px;
        font-family:'Times New Roman', Times, serif;
     
        
    }
    .btnsection:hover{
        height: 10vh;
        width: 25%;
     margin: auto;
        background-image: -webkit-gradient(linear,left top, left bottom,from(rgb(0, 43, 66)),to(rgb(0, 0, 0)));
        background-image: -webkit-linear-gradient(rgb(0, 43, 66),rgb(0, 0, 0));
        background-image: -o-linear-gradient(rgb(0, 43, 66),rgb(0, 0, 0));
        background-image: linear-gradient(rgb(0, 43, 66),rgb(0, 0, 0));
        color: white;
        font-size: 10px;
        margin-top: 20px;
        font-family:'Times New Roman', Times, serif;
        -webkit-transition: all .8s;
        -o-transition: all .8s;
        transition: all .8s;
        
    }}


